import * as React from 'react';
import { Link } from '@material-ui/core';
import './component-style.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconProp } from '@fortawesome/fontawesome-svg-core';

interface IProps {
  icon?: IconProp;
  label?: string;
  onClick: () => void;
}

export class TextButton extends React.Component<IProps> {
  shouldComponentUpdate(props: IProps, _state: any) {
    return this.props.icon !== props.icon || this.props.label !== props.label;
  }

  render(): JSX.Element {
    return (
      <Link className="text-button" onClick={() => this.props.onClick()}>
        {this.props.icon ? <FontAwesomeIcon icon={this.props.icon} /> : null}
        {this.props.icon ? <span style={{ marginLeft: '10px' }} /> : null}
        {this.props.label}
      </Link>
    );
  }
}
