import * as React from 'react';
import { Grid, TextField, InputAdornment } from '@material-ui/core';
import { Search } from '@material-ui/icons/';
import { TextButton } from './Components/TextButton';
import { PartialBenefitData } from './Models';
import { API } from '../../API';
import { TableComponent } from 'Components/UI/TableComponent';

const columns: any = [
  { name: 'category', title: 'Benefit Category' },
  { name: 'name', title: 'Benefit' },
  { name: 'limitType', title: 'Limit Type' },
  { name: 'limit', title: 'Limit' },
];

const columnExtensions: any = [
  { columnName: 'category', width: 200 },
  { columnName: 'limitType', width: 200 },
  { columnName: 'limit', width: 200 },
];

interface IProps {
  memberCardNo: string;
  setIsLoading: (isLoading: boolean) => void;
}

interface IStates {
  allMemberBenefits: PartialBenefitData[];
  filteredMemberBenefits: PartialBenefitData[];
  globalFilter: string;
  columnFilters: any;
}

export class MemberBenefits extends React.Component<IProps, IStates> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      allMemberBenefits: [],
      filteredMemberBenefits: [],
      globalFilter: '',
      columnFilters: [],
    };
  }

  componentDidMount() {
    const { memberCardNo } = this.props;
    if (memberCardNo && memberCardNo.length > 0) {
      this.loadBenefitsData();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.memberCardNo !== prevProps.memberCardNo) {
      this.loadBenefitsData();
    }
  }

  handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    this.setState(
      {
        globalFilter: value,
      },
      () => this.filterMemberBenefits(),
    );
  };

  /******* handles filters in table columns *******/
  handleFilterChange = (filters: any) => {
    this.setState(
      {
        columnFilters: filters,
      },
      () => this.filterMemberBenefits(),
    );
  };

  filterMemberBenefits = () => {
    const { allMemberBenefits, globalFilter, columnFilters } = this.state;

    let filterCategory: string = '';
    let filterName: string = '';
    let filterLimitType: string = '';
    let filterLimit: string = '';

    columnFilters.forEach(item => {
      if (item.columnName === 'category') {
        filterCategory = item.value.toLowerCase();
      }
      if (item.columnName === 'name') {
        filterName = item.value.toLowerCase();
      }
      if (item.columnName === 'limitType') {
        filterLimitType = item.value.toLowerCase();
      }
      if (item.columnName === 'limit') {
        filterLimit = item.value.toLowerCase();
      }
    });

    const containsGlobalFilter = (target: any) => {
      return (
        target &&
        target !== undefined &&
        target !== null &&
        target.toLowerCase().includes(globalFilter.toLowerCase())
      );
    };

    const contailsColumnFilters = (item: any) => {
      return (
        item.category &&
        item.category !== undefined &&
        item.category !== null &&
        item.category.toLowerCase().includes(filterCategory.toLowerCase()) &&
        item.name &&
        item.name !== undefined &&
        item.name !== null &&
        item.name.toLowerCase().includes(filterName.toLowerCase()) &&
        item.limitType &&
        item.limitType !== undefined &&
        item.limitType !== null &&
        item.limitType.toLowerCase().includes(filterLimitType.toLowerCase()) &&
        item.limit &&
        item.limit !== undefined &&
        item.limit !== null &&
        item.limit.toLowerCase().includes(filterLimit.toLowerCase())
      );
    };

    const filteredData = allMemberBenefits.filter(item => {
      return (
        (containsGlobalFilter(item.category) ||
          containsGlobalFilter(item.name) ||
          containsGlobalFilter(item.limitType) ||
          containsGlobalFilter(item.limit)) &&
        contailsColumnFilters(item)
      );
    });

    this.setState({
      filteredMemberBenefits: filteredData,
    });
  };

  loadBenefitsData(): void {
    const { memberCardNo, setIsLoading } = this.props;
    if (!memberCardNo || memberCardNo.length === 0) {
      return;
    }

    if (setIsLoading) {
      setIsLoading(true);
    }
    API.getMemberBenefits(memberCardNo).then(response => {
      if (response && response.error === undefined) {
        const allBenefits = this.combineNodes(
          response.filter(benefit => benefit.level == 6),
          response.filter(benefit => benefit.level == 7),
        ).map(benefit => {
          return {
            category: benefit.parent_name,
            name: benefit.name,
            limitType:
              benefit.limits.length > 0 ? benefit.limits[0].type : 'None',
            limit:
              benefit.limits.length > 0
                ? benefit.limits[0].limit + ' ' + benefit.limits[0].limit_unit
                : '---',
          };
        });

        this.setState(
          {
            allMemberBenefits: allBenefits,
          },
          () => {
            this.filterMemberBenefits();

            if (setIsLoading) {
              setIsLoading(false);
            }
          },
        );
      } else {
        if (setIsLoading) {
          setIsLoading(false);
        }
      }
    });
  }

  combineNodes = (parentNodes, childNodes) => {
    const nodes = parentNodes.reduce((result, parent) => {
      parent.children.map(child => {
        let foundChild = childNodes.find(childNode => child === childNode._id);

        if (foundChild) {
          let child = Object.assign({}, foundChild);

          child['parent_name'] = parent.name;
          child['parent_code'] = parent.code;
          child['disabled'] = false;

          result.push(child);
        }
      });

      return result;
    }, new Array());

    return nodes;
  };

  handleSorting = (sorting: any) => {
    const { allMemberBenefits } = this.state;
    if (sorting && sorting.length > 0) {
      const sortCriteria = sorting[0];
      const sortColumn = sortCriteria.columnName;
      const sortDirection = sortCriteria.direction;

      if (sortDirection === 'asc') {
        allMemberBenefits.sort((a, b) =>
          a[sortColumn] > b[sortColumn] ? 1 : -1,
        );
      } else {
        allMemberBenefits.sort((a, b) =>
          a[sortColumn] > b[sortColumn] ? -1 : 1,
        );
      }

      this.setState({
        allMemberBenefits,
      });
    }
  };

  render(): JSX.Element {
    const { filteredMemberBenefits, globalFilter, columnFilters } = this.state;
    return (
      <div className="MemberBenefitsPage section">
        <Grid container className="sub-section" spacing={5}>
          {this.renderSideBar()}
          <Grid item xs={10}>
            <Grid container>
              <TextField
                style={{ width: '100%' }}
                id="member-benefits-search"
                placeholder="Search members benefits"
                label=""
                margin="normal"
                variant="outlined"
                value={globalFilter}
                onChange={this.handleSearch}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Search style={{ color: 'rgba(54, 54, 54, 0.4)' }} />
                    </InputAdornment>
                  ),
                }}
              />
              <TableComponent
                id="member-benefits-table"
                rows={filteredMemberBenefits}
                columns={columns}
                message="No member benefits data"
                columnExtensions={columnExtensions}
                formattedColumns={{}}
                onClickRow={() => {}}
                defaultFilter={columnFilters}
                onFilterChange={this.handleFilterChange}
                onSortingChange={this.handleSorting}
                disableSelect
                disableSearch
              />
            </Grid>
          </Grid>
        </Grid>
        <div className="section" />
      </div>
    );
  }

  renderSideBar(): JSX.Element {
    const rightBorderStyle = {
      borderRight: '#3C394A22 solid 1px',
    };
    return (
      <Grid item xs={2} style={rightBorderStyle}>
        <div className="sub-section">
          <div className="light-text header-1">COVERAGE</div>
          <div className="sub-section">
            <TextButton
              label="Room and Board"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="APE"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="PPE"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Inpatient"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Outpatient"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Emergency"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
        </div>

        <div className="sub-section">
          <div className="light-text header-1">RIDERS</div>
          <div className="sub-section">
            <TextButton
              label="Dental"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Maternity"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Outpatient Medicine"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Executive Checkup"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
          <div className="sub-section">
            <TextButton
              label="Corporate Clinic"
              onClick={() => {
                this.loadBenefitsData();
              }}
            />
          </div>
        </div>
      </Grid>
    );
  }
}
