      pageHeader,
      steps,
      activeStep,
      clients_data,
      open,
      details_data,
      corporate_account,
      disabledNext,
      initial_load,
      loading_state,
      uploaded_data,
      columns_data,
      contracts_data,
      system_names,
      clients_data_maps,
      selected_data_map,
      gender_data,
      civil_status_data,
      plan_type_data,
      type_data,
      relationship_data,
      raw_uploaded_data,
      add_new_data_map,
      rows_start_at,
      filtered_raw_data,
      client_maps,
      modalTitle,
      modalMessage,
      isOpenModal,
      isOpenErrorWithListModal,
      error_list,
      batchnames,
      plan_type_options,
      civil_status_options,
      relationship_options,
